import React, {useState} from "react";

import './style.css'
import { XCircle} from "react-bootstrap-icons";
import {Link} from "react-router-dom";

export default function MyCard ({data , handleCardClick , state}) {

    const handleClick = (data) => {
        handleCardClick(data)
    }

    return <div id={'card'} className={'border border-success p-1 rounded-2 text-end'}>
            <p>{data['نام درس']}</p>
            {state && <li id={'li-card'} className={'text-warning'}>{state === "FARD" ? "فرد" : "زوج" }</li>}
            <li id={'li-card'}> واحد: {data["واحد"]}</li>
            <li id={'li-card'}>گروه: {data["گروه"]}</li>
            <div className={'text-start'}>
                <Link to={''}  onClick={() => handleClick(data)}> <XCircle color={'red'} /> </Link>
            </div>
    </div>
}